import React, { Component } from 'react';
import { Event } from 'react-socket-io';
import './Tessel.css';

class Tessel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      thres: 0.045,
      x_orient: "",
      y_orient: "",
      flashed: false,
    };
  }

  handleData = (obj) => {
    const { x, y } = obj;
    const thres = this.state.thres;
    this.setState({ x_orient: "" });
    this.setState({ y_orient: "" });

    if (Math.abs(x) > thres) {
        if (x > 0) this.setState({ x_orient: "pos" });
        else this.setState({ x_orient: "neg" });
    }
    if (Math.abs(y) > thres) {
        if (y > 0) this.setState({ y_orient: "pos" });
        else this.setState({ y_orient: "neg" });
    }
  }

  handleAddSen = () => {
    const thres = (this.state.thres - 0.0005) === 0 ? 0 : this.state.thres - 0.0005;
    this.setState({
      thres,
    });
  }

  handleSubSen = () => {
    const thres = (this.state.thres + 0.0005) === 1 ? 1 : this.state.thres + 0.0005;
    this.setState({
      thres,
    });
  }

  handleResSen = () => {
    const thres = 0.045;
    this.setState({
      thres,
    });
  }

  handleFlashToggle = () => {
    this.setState({
      flashed: !this.state.flashed,
    });
  }

  render() {
    return(
      <div className="tessel">
        <Event event="newdata" handler={ this.handleData } />
        <div className="camera">
           <img src="/stream" className="web-cam" alt="web-cam" />
        </div>
        <div className="accel-value">
          <span>X: { this.state.x_orient } Y: { this.state.y_orient }</span>
        </div>
        <div className="sens-button">
          <button onClick={ this.handleAddSen }>+</button>
          <button onClick={ this.handleSubSen }>-</button>
          <button onClick={ this.handleResSen }>Reset</button>
        </div>
        <div className={ this.state.flashed ? "flash-btn clicked" : "flash-btn" }>
          <button onClick={ this.handleFlashToggle }>Flash</button>
        </div>
      </div>
    );
  }
}

export default Tessel;



